	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"With Spring and Hibernate on your stack, your application's bytecode is likely enhanced or manipulated at runtime. This session examines three common byte code manipulation frameworks: ASM, CGLib, and Javassist (Java Programming Assistant). We will discuss how these tools work and why frameworks like Spring use them. You will learn enough to begin integrating these frameworks directly into your own code.","meta":{"duration":"PT55M1S"},"presenter":{"name":"Ashley Puls"},"source":{"id":"39kdr1mNZ_s","provider":"youtube"},"slug":"living-in-the-matrix-with-bytecode-manipulation","tag":["Spring Framework","Java"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/39kdr1mNZ_s/default.jpg","height":90,"width":120}},"title":"Living in the Matrix with Bytecode Manipulation"}}]}},"pathContext":{"speaker":"Ashley Puls"}}

